@import url("https://fonts.googleapis.com/css?family=Raleway");

* {
  font-family: "Raleway", sans-serif;
}

/* QUESTIONS */

.container {
  position: absolute;
  width: auto;
  height: auto;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  padding: 10px;
  margin-right: 10%;
  margin-left: 1%;
}

.title {
  position: relative;
  background-color: rgb(29, 126, 37);
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  border-radius: 5px;
}

.question {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 5px;
}

.questionBox {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 1em;
  padding: 15px;
  box-sizing: border-box;
  background-color: rgb(250, 250, 250);
}

/* ANSWERS */

.answerBtn {
  position: relative;
  padding: 10px;
  background-color: rgb(250, 151, 37);
  display: inline-block;
  margin: 10px;
  outline: none;
  border: none;
  font-size: 1em;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
}

.score-board {
  position: relative;
  width: 100%;
  height: auto;
}

.score {
  position: absolute;
  width: auto;
  max-width: 1024px;
  font-size: 2em;
  top: 200px;
  text-align: center;
}

.playBtn {
  position: absolute;
  font-size: 1.4em;
  padding: 10px;
  top: 350px;
  background-color: rgb(46, 182, 46);
  color: rgb(255, 255, 255);
  width: 150px;
  left: 0px;
  right: 0px;
  margin: auto;
  outline: none;
  cursor: pointer;
}

@media (min-width: 350px) {
  .container {
    position: absolute;
    width: auto;
    height: auto;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    padding: 10px;
    margin-top: 2%;
    margin-right: 6%;
    margin-left: 1%;
  }

  .score-board {
    position: relative;
    width: 315px;
    height: auto;
  }
  .score {
    position: absolute;
    width: auto;
    font-size: 2em;
    top: 200px;
    text-align: center;
  }
}

@media (min-width: 768px) {
  .container {
    position: absolute;
    width: auto;
    height: auto;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
    padding: 10px;
    margin-right: 5%;
    margin-left: 1%;
  }

  .score-board {
    position: relative;
    width: 760px;
    height: auto;
  }

  .score {
    position: absolute;
    width: 100%;
    font-size: 2em;
    top: 250px;
    text-align: center;
  }
}
